:root {
  --loaderCircleColor: var(--color-gray4, #ccc);
  --loaderCircle2Color: var(--color-primary, #4799e3);
  --loaderCircle3Color: var(--color-primary, #555123);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
html[theme='dark'] {
  --loaderCircleColor: #505050;
}

.loader-circle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &--empty {
    margin-bottom: 30px;
  }

  &__text {
    margin-top: 16px;
    font-family: var(--font-general);
    font-size: 14px;
    line-height: 19px;
    font-style: italic;
    font-weight: normal;
    opacity: 0.3;
  }
}

.loader {
  position: relative;
  display: block;
  width: 32px;
  height: 32px;

  &--rotation {
    animation: rotation 1s infinite linear;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &__circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke: var(--loaderCircleColor);
    stroke-width: 3px;
    stroke-linecap: round;

    &--inset {
      stroke-dasharray: 56;
      stroke-dashoffset: calc(56 - (56 * var(--loading)) / 100);
      stroke: var(--loaderCircle2Color);
    }

    &--inset {
      stroke-dasharray: 56;
      stroke-dashoffset: calc(56 - (56 * var(--loading)) / 100);
      stroke: var(--loaderCircle3Color);
    }
  }

  @keyframes rotation {
    from {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    }
    to {
      -webkit-transform: rotate(359deg);
      -webkit-transform: rotate(359deg);
      -moz-transform: rotate(359deg);
      -ms-transform: rotate(359deg);
      -o-transform: rotate(359deg);
      transform: rotate(359deg);
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    }
  }
}
